          </b-badge>

        </template> -->
        <!-- column: acciones -->
        <template #cell(acciones)="row">
          <b-button
            variant="outline-success"
            size="sm"
            @click="verFormulario(row.item)"
          >cargar formulario
          </b-button>
        </template>
