<script>
import {
  BCard, BTable, BSpinner, BRow, BCol, BButton,
} from 'bootstrap-vue'
// import store from '@/store'

export default {
  components: {
    BCard,
    BTable,
    BSpinner,
    BRow,
    BCol,
    BButton,

  },
  data() {
    return {
      isBusy: false,
      items: [],
      fields: [
        { key: 'id', label: 'ID', sortable: true },
        { key: 'codigo', label: 'CÓDIGO', sortable: true },
        { key: 'nombre', label: 'NOMBRE', sortable: true },
        { key: 'acciones' },
      ],
    }
  },
  mounted() {
    this.$http.get('/recursos/formulario').then(res => {
      console.log(res)
      this.items = res.data
    })
  },
  methods: {

    verFormulario(item) {
      if (item.codigo === 'HOSTIGAMIENTO-DENUNCIA') {
        this.$router.push({ name: 'vista-anonima-x', params: { id_formulariox: item.id } })
        return
      }
      this.$router.push({ name: 'formulario-x', params: { id_formulariox: item.id } })
    },
    nuevoFormulario() {
      const DATA = [
        {
          nombre: 'FORMULARIO SIN TÍTULO',
          version: 'v1',
          descripcion: 'Sin descripción',
          secciones: [
            {
              titulo: 'Sección sin titulo',
              subtitulo: 'Sin subtítulo ',
              preguntas: [],
            },
          ],
        },
      ]
      this.$http.post('/recursos/formulario', DATA).then(res => {
        console.log('Respuesta Creacion formulario: ', res)
        this.$router.push({ name: 'nuevo-formulario-index', params: { id_formulario: res.data.id } })
      })
    },
    /* async myProvider(ctx) {
      console.log('CTX: ', ctx)
      const promise = store.dispatch('ingresos-list-store/getIngresosMedicamentos', { queryParams: ctx })

      // Must return a promise that resolves to an array of items
      return promise.then(response => {
        console.log('responseGetIngresosMedicamentos: ', response)
        const {
          data, total, from, to,
        } = response.data
        // Pluck the array of items off our axios response
        const items = data
        this.totalRows = total || 0
        // this.from = meta.pagination.count
        this.from = from || 0
        this.to = to || 0

        // Must return an array of items or an empty array if an error occurred
        return items || []
      })
    }, */
  },
}
